import {Component} from '@angular/core';
import {Router, ActivatedRoute, ParamMap, NavigationEnd} from '@angular/router';
import {Platform, MenuController} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
import {AuthService} from '../services/auth.service';
import { EventService } from '../services/event.service';

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html'
})
export class AppComponent {
    public afterAuth = [
        {
            title: 'Home',
            url: '/home',
            icon: 'home'
        },
        {
            title: 'Near Me',
            url: '/nearme',
            icon: 'locate'
        },
        {
            title: 'My Doctor',
            url: '/doctors',
            icon: 'medkit'
        },
        {
            title: 'Prescriptions',
            url: '/prescription',
            icon: 'document'
        },
        {
            title: 'Report',
            url: '/report',
            icon: 'clipboard'
        },
        {
            title: 'Diabetics',
            url: '/diabetes',
            icon: 'ribbon'
        },
        {
            title: 'Blood Pressure',
            url: '/blood-pressure',
            icon: 'heart'
        },
        {
            title: 'Logout',
            url: '/logout',
            icon: 'power'
        }
    ];

    public beforeAuth = [
      {
        title: 'Login',
        url: '/login',
        icon: 'log-in'
      },
      {
        title: 'Registration',
        url: '/registration',
        icon: 'contact'
      },
      {
        title: 'Forget Password',
        url: '/forget-password',
        icon: 'key'
      }];
    
    public appPages = [];

    hide_menu = ['/login', '/registration', '/activation'];
    user: any;

    constructor(
        private platform: Platform,
        private splashScreen: SplashScreen,
        private statusBar: StatusBar,
        public authService: AuthService,
        private router: Router,
        private menuCtrl: MenuController,
        private events: EventService
    ) {
        this.initializeApp();
        this.appPages = this.beforeAuth;
        this.events.dispatcher.subscribe(response => {
          if(response.auth) {
            this.user = response.auth;
            this.appPages = this.afterAuth;
          }
          else {
           this.appPages = this.beforeAuth;   
           this.user = null;
          }
        })
    }

    initializeApp() {
        this.platform.ready().then(() => {
            this.loadAuth();
            this.statusBar.styleDefault();
            this.splashScreen.hide();
        });
    }

    loadAuth() {
        this.authService.getAuth().subscribe(auth => {
            if (auth) {
                if (auth.token) {
                   this.loadUser();
                }
                else if (auth.active) {
                    this.router.navigateByUrl('/login');
                }
                else {
                    this.router.navigateByUrl('/activation');
                }
            }
            else {
                this.router.navigateByUrl('/login');
            }
            console.log(auth);
        },
        err => {
            console.log(err);
        });
    }

    public loadUser() {
      this.authService.getUser().subscribe(user => {
        this.user = user;
        this.events.emitAuthEvent({auth: user});
        this.router.navigateByUrl('/');
      },
      err => {
        console.log(err);
        this.router.navigateByUrl('/');
      });
    }
}
