import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import { AuthService } from '../../services/auth.service';
import { DataService } from '../../services/data.service';
import { HelperService } from '../../services/helper.service';
import { Auth } from '../models/auth';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss'],
})
export class SignUpComponent implements OnInit {
  registration = {type: 'Patient', full_name: '', email: '', phone: '', password: '', password_confirmation: '', gender: '', referrer_id: ''}
  submitted = false;
  errors: Array<any>;
  auth = new Auth();

  constructor(private authService: AuthService, private dataService: DataService, private router: Router,
    private helperService: HelperService) { }

  ngOnInit() {}

  public submitRegistration(is_valid) {
  	this.submitted = true;
    if(is_valid) {
      this.authService.registration(this.registration).subscribe(resp => {
      	if(resp.success) {
      	  this.auth.active = false;	
          this.authService.updateAuth(this.auth);
          this.router.navigateByUrl('/activation')
      	}
      	else {
	       this.errors = this.dataService.getErrors(resp.errors);
      	}
      },
      err => {
        this.helperService.presentToast('Something went wrong. Please try again after sometime!', 'danger');
      });
    }
  }

}
