import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Jsonp } from '@angular/http';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { AuthService } from './auth.service';
import { Auth } from '../app/models/auth';

@Injectable({
  providedIn: 'root'
})
export class DiabetesService {
  api_url = 'http://0.0.0.:3000/api/v1/';

  constructor(private http: HttpClient, private jsonp: Jsonp, private authService: AuthService) { }

  public getDiabetes(): Observable<any> {
    return this.http.get(this.api_url + 'diabetics', {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public recordDiabetic(data): Observable<any> {
    return this.http.post(this.api_url + 'diabetics', {diabetic: data}, {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public removeDiabetic(id): Observable<any> {
    return this.http.delete(this.api_url + 'diabetics/' + id, {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public getGraph(): Observable<any> {
    return this.http.get(this.api_url + 'diabetics/graph', {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public getDiabetesGraph(id): Observable<any> {
    return this.http.get(this.api_url + 'prescriptions/' + id, {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }
}