import {Component, OnInit} from '@angular/core';
import {ToastController} from '@ionic/angular';
import {ReportService} from '../../services/report.service';
import {PrescriptionService} from '../../services/prescription.service';
import {Report} from '../models/report';

@Component({
    selector: 'app-report',
    templateUrl: './report.component.html',
    styleUrls: ['./report.component.scss'],
})
export class ReportComponent implements OnInit {
    submitted = false;
    prescriptions = [];
    report = new Report();
    reports: Array<Report>;
    formData = new FormData();

    constructor(public reportService: ReportService, public toastCtl: ToastController, public prsService: PrescriptionService) {
    }

    ngOnInit() {
        this.loadPrescriptions();
    }

    public loadReports() {
        this.reportService.getReports().subscribe(resp => {
                this.reports = resp.reports;
            },
            err => {
                this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
                console.log(err);
            });
    }

    public submitReport(is_valid) {
        this.submitted = true;
        if (is_valid) {
            console.log(this.report);
            for (var key in this.report) {
                this.formData.delete(key);
                this.formData.append(key, this.report[key]);
            }
            this.createReport();
        }
    }

    public createReport() {
        this.reportService.recordReport(this.formData).subscribe(resp => {
                this.presentToast('Report recorded successfully.');
                this.report = new Report();
                this.submitted = false;
            },
            err => {
                this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
                console.log(err);
            });
    }

    public loadPrescriptions() {
        this.prsService.getPrescriptions().subscribe(resp => {
                this.prescriptions = resp.prescriptions;
            },
            err => {

            });
    }

    public removeReport(id, index) {
        if (confirm('Are you sure?')) {
            this.reportService.removeReport(id).subscribe(resp => {
                    if (resp.success) {
                        this.reports = this.reports.slice(index);
                    }
                    else {
                        this.presentToast(resp.message, 'danger');
                    }
                },
                err => {
                    this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
                    console.log(err);
                });
        }
    }

    public uploadFile($event) {
        var file = $event.target.files[0];
        this.formData.delete('file');
        this.formData.append('file', file, file.name);
    }

    segmentChanged(ev: any) {
        console.log(ev);
    }

    async presentToast(message, color = 'light') {
        const toast = await this.toastCtl.create({
            message: message,
            duration: 5000,
            showCloseButton: true,
            position: 'top',
            closeButtonText: 'Close',
            color: color
        });
        toast.present();
    }

}
