import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Jsonp} from '@angular/http';
import {BehaviorSubject, Observable, of} from 'rxjs';
import {Auth} from '../app/models/auth';

@Injectable({
    providedIn: 'root'
})
export class AuthService {
    api_url = 'http://0.0.0.:3000/api/v1/';

    constructor(private http: HttpClient, private jsonp: Jsonp) {
    }

    public registration(data): Observable<any> {
        return this.http.post(this.api_url + 'users/registration', {user: data});
    }

    public login(data): Observable<any> {
        return this.http.post(this.api_url + 'users/login', {user: data});
    }

    public logout() {
        localStorage.setItem('auth', JSON.stringify(new Auth));
    }

    public activateAuth(data): Observable<any> {
        return this.http.post(this.api_url + 'users/activate', data);
    }

    updateAuth(auth: Auth) {
        localStorage.setItem('auth', JSON.stringify(auth));
    }

    getAuth() {
        let auth = localStorage.getItem('auth');
        if (auth) {
            return of(JSON.parse(auth));
        }
        else {
            return of(new Auth());
        }
    }

    public getUser(): Observable<any> {
        return this.http.get(this.api_url + 'users', {headers: {Authorization: `Bearer ${this.getToken()}`}});
    }

    getToken() {
        let auth = localStorage.getItem('auth');
        if (auth) {
            return JSON.parse(auth).token;
        }
        else {
            return '';
        }
    }
}
