import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Jsonp} from '@angular/http';
import {BehaviorSubject, Observable, of} from 'rxjs';
import {AuthService} from './auth.service';
import {Auth} from '../app/models/auth';

@Injectable({
    providedIn: 'root'
})
export class ReportService {
    api_url = 'http://0.0.0.0:3000/api/v1/';

    constructor(private http: HttpClient, private jsonp: Jsonp, private authService: AuthService) {
    }

    public getReports(): Observable<any> {
        return this.http.get(this.api_url + 'reports', {headers: {Authorization: `Bearer ${this.authService.getToken()}`}});
    }

    public getReport(id): Observable<any> {
        return this.http.get(this.api_url + 'reports/' + id, {headers: {Authorization: `Bearer ${this.authService.getToken()}`}});
    }

    public recordReport(data): Observable<any> {
        return this.http.post(this.api_url + 'reports/', data, {headers: {Authorization: `Bearer ${this.authService.getToken()}`}});
    }

    public removeReport(id): Observable<any> {
        return this.http.delete(this.api_url + 'reports/' + id, {headers: {Authorization: `Bearer ${this.authService.getToken()}`}});
    }
}
