import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {ReportService} from '../../../services/report.service';
import {HelperService} from '../../../services/helper.service';

import {Report} from '../../models/report';

@Component({
    selector: 'app-details',
    templateUrl: './details.component.html',
    styleUrls: ['./details.component.scss'],
})
export class ReportDetailsComponent implements OnInit {
    report = new Report();
    image = '/assets/shapes.svg';

    constructor(public reportService: ReportService, public helperService: HelperService, private route: ActivatedRoute) {
    }

    ngOnInit() {
        this.route.params.subscribe(params => {
            this.loadReport(params['id']);
        });
    }

    public loadReport(id) {
        this.reportService.getReport(id).subscribe(resp => {
            this.report = resp.report;
            if (resp.documents.length > 0) {
                this.image = resp.documents[0];
            }
        }, err => {
            this.helperService.presentToast('Something went wrong. Please try again after sometimes!');
        });
    }
}
