export class BloodPresure {
  id: number;
  high: number;
  low: number;
  pulse: number;
  measurement_date = this.currentDate();
  created_at: string;
  updated_at: string;
  patient_id: number;

  public currentDate() {
  	let today = new Date();
  	let dd = today.getDate();
  	let mm = today.getMonth() + 1;
  	let yyyy = today.getFullYear();
  	let st_date = dd < 10 ? '0' + dd : '' + dd;
  	let st_month = mm < 10 ? '0' + mm : '' + mm;
  	return yyyy + '-' + st_month + '-' + st_date;
  }
}