import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {PrescriptionService} from '../../../services/prescription.service';
import {HelperService} from '../../../services/helper.service';

import {Prescription} from '../../models/prescription';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss'],
})

export class PrescriptionDetailsComponent implements OnInit {
    prescription = new Prescription();
    image = '/assets/shapes.svg';

  constructor(public PrescriptionService: PrescriptionService, public helperService: HelperService, private route: ActivatedRoute) { }

  ngOnInit() {
      this.route.params.subscribe(params => {
         this.loadPrescription(params['id']);
      });
  }

    public loadPrescription(id) {
        this.PrescriptionService.getPrescription(id).subscribe(resp => {
            this.prescription = resp.prescription;
            if (resp.documents.length > 0) {
                this.image = resp.documents[0];
            }
        }, err => {
            this.helperService.presentToast('Something went wrong. Please try again after sometimes!');
        });
    }

}
