import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {UserService} from '../../../services/user.service';
import {HelperService} from '../../../services/helper.service';

import {User} from '../../models/user';
import {ReportService} from '../../../services/report.service';

@Component({
    selector: 'app-details',
    templateUrl: './details.component.html',
    styleUrls: ['./details.component.scss'],
})
export class DoctorDetailsComponent implements OnInit {
    doctor = new User();
    image = '';

    constructor(public userService: UserService, public helperService: HelperService, private route: ActivatedRoute) {
    }

    ngOnInit() {
        this.route.params.subscribe(params => {
            this.loadDoctor(params['id']);
        });
    }

    public loadDoctor(id) {
        this.userService.getDoctor(id).subscribe(resp => {
            this.doctor = resp.doctor;
            this.image = resp.avatar;
        }, err => {
            this.helperService.presentToast('Something went wrong. Please try again after sometimes!');
        });
    }

}
