import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { MenuController} from '@ionic/angular';

import { AuthService } from '../../services/auth.service';
import { HelperService } from '../../services/helper.service';
import { EventService } from '../../services/event.service';
import { Auth } from '../models/auth';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.scss'],
})
export class SignInComponent implements OnInit {
  login = {email: '', password: ''}
  submitted = false;
  auth = new Auth();

  constructor(private authService: AuthService, private router: Router, public helperService: HelperService,
    private menuCtrl: MenuController, private events: EventService) { }

  ngOnInit() {}

  public submitLogin(is_valid) {
  	this.submitted = true;
    if(is_valid) {
      this.authService.login(this.login).subscribe(resp => {
      	if(resp.success) {
           this.auth.token = resp.auth_token; 
           this.authService.updateAuth(this.auth);
           this.events.emitAuthEvent({auth: resp.user});
           this.router.navigateByUrl('/');
      	}
      	else {
      	  this.helperService.presentToast(resp.message, 'danger');
      	}
      },
      err => {
        this.helperService.presentToast(err.message, 'danger');
      });
    }
  }
}
