import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Jsonp } from '@angular/http';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { AuthService } from './auth.service';
import { Auth } from '../app/models/auth';

@Injectable({
  providedIn: 'root'
})

export class BloodPresureService {
  api_url = 'http://0.0.0.:3000/api/v1/';

  constructor(private http: HttpClient, private jsonp: Jsonp, private authService: AuthService) { }

  public getBloodPresures(): Observable<any> {
    return this.http.get(this.api_url + 'blood_pressures', {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public recordBloodPresure(data): Observable<any> {
    return this.http.post(this.api_url + 'blood_pressures', {blood_pressure: data}, {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public removeBloodPresure(id): Observable<any> {
    return this.http.delete(this.api_url + 'blood_pressures/' + id, {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public getGraph(): Observable<any> {
    return this.http.get(this.api_url + 'blood_pressures/graph', {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }

  public getBloodPresureGraph(id): Observable<any> {
    return this.http.get(this.api_url + 'prescriptions/' + id, {headers: { Authorization: `Bearer ${this.authService.getToken()}` }})
  }
}