import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Jsonp} from '@angular/http';
import {BehaviorSubject, Observable, of} from 'rxjs';
import {AuthService} from './auth.service';
import {Auth} from '../app/models/auth';

@Injectable({
    providedIn: 'root'
})
export class UserService {
    api_url = 'http://0.0.0.0:3000/api/v1/';

    constructor(private http: HttpClient, private jsonp: Jsonp, private authService: AuthService) {
    }

    public getDoctors(name): Observable<any> {
        let params = '';
        if (name) {
            params = 'name=' + name;
        }
        return this.http.get(this.api_url + 'users/doctors?' + params, {headers: {Authorization: `Bearer ${this.authService.getToken()}`}});
    }

    public getDoctor(id): Observable<any> {
        return this.http.get(this.api_url + 'users/' + id + '/show_doctor', {headers: {Authorization: `Bearer ${this.authService.getToken()}`}});
    }
}
