import { DiabetesService } from '../../services/diabetes.service';
import { ToastController } from '@ionic/angular';
import { Diabetes } from '../models/diabetes';
import { Component, OnInit, ViewChild } from '@angular/core';
import {Chart} from 'chart.js';

@Component({
  selector: 'app-diabetes',
  templateUrl: './diabetes.component.html',
  styleUrls: ['./diabetes.component.scss'],
})
export class DiabetesComponent implements OnInit {
  submitted = false;
  diabetic = new Diabetes();
  diabetics: Array<Diabetes>;
  lineChart: any;
  @ViewChild('lineCanvas') lineCanvas;

  constructor(private diaService: DiabetesService, public toastCtl: ToastController) { }

  ngOnInit() {

  }

  public loadDiabetics() {
    this.diaService.getDiabetes().subscribe(resp => {
       this.diabetics = resp.diabetics;
    }, 
    err => {
      this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
      console.log(err);
    })
  }

  public submitDiabetic(is_valid) {
  	this.submitted = true;
  	if(is_valid) {
      this.diaService.recordDiabetic(this.diabetic).subscribe(resp => {
        this.presentToast('Diabetics information recorded.');
        this.diabetic = new Diabetes();
        this.submitted = false;
      },
      err => {
        this.presentToast('Something went wrong. Please try after sometimes', 'danger');
      	console.log(err);
      })
  	}
  }

  public removeDiabetic(id, index) {
    if(confirm('Are you sure?')) {
      this.diaService.removeDiabetic(id).subscribe(resp => {
        if(resp.success) {
          this.diabetics = this.diabetics.slice(index);
        }
        else {
          this.presentToast(resp.message, 'danger');
        }
      },
      err => {
        this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
        console.log(err);
      })
    }
  }

  public loadDiabeticChart() {
    this.diaService.getGraph().subscribe(resp => {
       console.log(resp);
       this.loadChart(resp.labels, resp.before, resp.after);
    }, 
    err => {
      this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
    })
  }

  public loadChart(labels, before, after) {
      this.lineChart = new Chart(this.lineCanvas.nativeElement, {

          type: 'line',
          data: {
              labels: labels,
              datasets: [
                  {
                      label: 'Before Meal',
                      fill: false,
                      lineTension: 0.1,
                      backgroundColor: 'rgba(75,192,192,0.4)',
                      borderColor: 'rgba(75,192,192,1)',
                      borderCapStyle: 'butt',
                      borderDash: [],
                      borderDashOffset: 0.0,
                      borderJoinStyle: 'miter',
                      pointBorderColor: 'rgba(75,192,192,1)',
                      pointBackgroundColor: '#fff',
                      pointBorderWidth: 1,
                      pointHoverRadius: 5,
                      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                      pointHoverBorderColor: 'rgba(220,220,220,1)',
                      pointHoverBorderWidth: 2,
                      pointRadius: 1,
                      pointHitRadius: 10,
                      data: before,
                      spanGaps: false,
                  },
                  {
                      label: 'After Meal',
                      fill: false,
                      lineTension: 0.1,
                      backgroundColor: 'rgba(35, 144, 196, 0.4)',
                      borderColor: 'rgba(35, 144, 196, 1)',
                      borderCapStyle: 'butt',
                      borderDash: [],
                      borderDashOffset: 0.0,
                      borderJoinStyle: 'miter',
                      pointBorderColor: 'rgba(75,192,192,1)',
                      pointBackgroundColor: '#fff',
                      pointBorderWidth: 1,
                      pointHoverRadius: 5,
                      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                      pointHoverBorderColor: 'rgba(220,220,220,1)',
                      pointHoverBorderWidth: 2,
                      pointRadius: 1,
                      pointHitRadius: 10,
                      data: after,
                      spanGaps: false,
                  }
              ]
          }
      });
  }

  segmentChanged(ev: any) {
    console.log(ev);
  }

  async presentToast(message, color = 'light') {
    const toast = await this.toastCtl.create({
      message: message,
      duration: 5000,
      showCloseButton: true,
      position: 'top',
      closeButtonText: 'Close',
      color: color
    });
    toast.present();
  }
}
