import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ToastController } from '@ionic/angular';

import { DataService } from '../../services/data.service';
import { AuthService } from '../../services/auth.service';
declare var google;


@Component({
  selector: 'app-near-me',
  templateUrl: './near-me.component.html',
  styleUrls: ['./near-me.component.scss'],
})
export class NearMeComponent implements OnInit {
  @ViewChild('map') mapElement: ElementRef;
  map: any;

  constructor(private dataService: DataService, private authService: AuthService, public toastCtl: ToastController) { }

  ngOnInit() {
    this.loadMap();
    this.loadNearOrganization();
  }

  public loadMap() {
    this.map = new google.maps.Map(this.mapElement.nativeElement, {
      center: {lat: 23.8103, lng: 90.4125},
      zoom: 8
    });
  }

  public addMaker(lat, lng, options) {
    let marker = new google.maps.Marker({
      map: this.map,
      animation: google.maps.Animation.DROP,
      position: new google.maps.LatLng(lat, lng)
    });
    let infowindow = new google.maps.InfoWindow({content: '<strong>' + options.name + '</strong><br>' + options.address + '<br>'});
    google.maps.event.addListener(marker, 'click', () => {
      infowindow.open(this.map, marker);
    });
  }

  public loadNearOrganization() {
    this.dataService.nearMeOrganization().subscribe(resp => {
      if(resp.organizations.length > 0) {
         for(let ind in resp.organizations) {
           let org = resp.organizations[ind];
           if(org.lat && org.long) {
              this.addMaker(org.lat, org.long, {name: org.name, address: org.address});
           }
         }
      }
    },
    err => {
      this.presentToast('Something went wrong. Please try after sometimes!');
    });
  }

  async presentToast(message, color = 'light') {
    const toast = await this.toastCtl.create({
      message: message,
      duration: 5000,
      showCloseButton: true,
      position: 'top',
      closeButtonText: 'Close',
      color: color
    });
    toast.present();
  }

}
