import {Component, OnInit} from '@angular/core';
import {ToastController} from '@ionic/angular';

import {UserService} from '../../services/user.service';

@Component({
    selector: 'app-doctor',
    templateUrl: './doctor.component.html',
    styleUrls: ['./doctor.component.scss'],
})
export class DoctorComponent implements OnInit {
    doctors = [];
    name: string;
    submitted = false;

    constructor(private userService: UserService, public toastCtl: ToastController) {
    }

    ngOnInit() {
        this.loadDoctors();
    }

    public submitSearch() {
        this.loadDoctors(this.name);
    }

    public loadDoctors(opt = null) {
        this.userService.getDoctors(opt).subscribe(resp => {
                this.doctors = resp.doctors;
            },
            err => {
                this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
                console.log(err);
            });
    }

    async presentToast(message, color = 'light') {
        const toast = await this.toastCtl.create({
            message: message,
            duration: 5000,
            showCloseButton: true,
            position: 'top',
            closeButtonText: 'Close',
            color: color
        });
        toast.present();
    }
}
