export class Report {
  id: number;
  date = this.currentDate();
  description :string;
  file: string;
  title: string;	
  visible = true;
  created_at: string;
  updated_at: string;
  doctor_id: number;
  patient_id: number;
  prescription_id: number;

  public currentDate() {
    let today = new Date();
    let dd = today.getDate();
    let mm = today.getMonth() + 1;
    let yyyy = today.getFullYear();
    let st_date = dd < 10 ? '0' + dd : '' + dd;
    let st_month = mm < 10 ? '0' + mm : '' + mm;
    return yyyy + '-' + st_month + '-' + st_date;
  }
}