import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {PrescriptionComponent} from './prescription/prescription.component';
import {DiabetesComponent} from './diabetes/diabetes.component';
import {SignUpComponent} from './sign-up/sign-up.component';
import {SignInComponent} from './sign-in/sign-in.component';
import {ForgetPasswordComponent} from './forget-password/forget-password.component';
import {BloodPressureComponent} from './blood-pressure/blood-pressure.component';
import {ActivationComponent} from './activation/activation.component';
import {NearMeComponent} from './near-me/near-me.component';
import {DoctorComponent} from './doctor/doctor.component';
import {LogoutComponent} from './logout/logout.component';
import {ReportComponent} from './report/report.component';
import {ReportDetailsComponent} from './report/details/details.component';
import {PrescriptionDetailsComponent} from './prescription/details/details.component';
import {DoctorDetailsComponent} from './doctor/details/details.component';

const routes: Routes = [
    {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
    },
    {
        path: 'home',
        loadChildren: './home/home.module#HomePageModule'
    },
    {
        path: 'list',
        loadChildren: './list/list.module#ListPageModule'
    },
    {
        path: 'prescription',
        component: PrescriptionComponent
    },
    {
        path: 'prescription/:id/details',
        component: PrescriptionDetailsComponent
    },
    {
        path: 'diabetes',
        component: DiabetesComponent
    },
    {
        path: 'registration',
        component: SignUpComponent
    },
    {
        path: 'login',
        component: SignInComponent
    },
    {
        path: 'forget-password',
        component: ForgetPasswordComponent
    },
    {
        path: 'blood-pressure',
        component: BloodPressureComponent
    },
    {
        path: 'report',
        component: ReportComponent
    },
    {
        path: 'report/:id/details',
        component: ReportDetailsComponent
    },
    {
        path: 'activation',
        component: ActivationComponent
    },
    {
        path: 'nearme',
        component: NearMeComponent
    },
    {
        path: 'doctors',
        component: DoctorComponent
    },
    {
        path: 'doctors/:id/details',
        component: DoctorDetailsComponent
    },
    {
        path: 'logout',
        component: LogoutComponent
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
