import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Jsonp } from '@angular/http';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { AuthService } from './auth.service';
import { Auth } from '../app/models/auth';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  api_url = 'http://0.0.0.:3000/api/v1/';

  constructor(private http: HttpClient, private jsonp: Jsonp, private authService: AuthService) { }

  public getErrors(errors) {
  	let sm_errors = [];
    let error_fields = Object.keys(errors);
    for(let field of error_fields) {
   	 sm_errors.push(field + " " + errors[field].join(','))
    }
    return sm_errors;
  }

  public nearMeOrganization(): Observable<any> {
    return this.http.get(this.api_url + 'users/nearest_organizations', {headers: { Authorization: `Bearer ${this.authService.getToken()}` }});
  }
}
