import {Component, OnInit} from '@angular/core';
import {any} from 'codelyzer/util/function';

import { PrescriptionService } from '../../services/prescription.service';
import { HelperService } from '../../services/helper.service';
declare var $;

@Component({
    selector: 'app-prescription',
    templateUrl: './prescription.component.html',
    styleUrls: ['./prescription.component.scss'],
})
export class PrescriptionComponent implements OnInit {
    constructor(private prescriptionService: PrescriptionService, private helperService: HelperService) {}
    prescriptions = [];
    ngOnInit() {}

    public uploadFile(event) {
      $('#exampleModal').modal({backdrop: false});
    }

    loadPrescription() {
      this.prescriptionService.getPrescriptions().subscribe(resp => {
        this.prescriptions = resp.prescriptions;
      },
      err => {
        this.helperService.presentToast('Something went wrong. Please try again after sometimes!', 'danger');
      });
    }
}
