import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import { AuthService } from '../../services/auth.service';
import { DataService } from '../../services/data.service';
import { HelperService } from '../../services/helper.service';
import { Auth } from '../models/auth';

@Component({
  selector: 'app-activation',
  templateUrl: './activation.component.html',
  styleUrls: ['./activation.component.scss'],
})
export class ActivationComponent implements OnInit {
  code: string;
  auth = new Auth();
  submitted = false;

  constructor(private authService: AuthService, private router: Router, private helperService: HelperService) { }

  ngOnInit() {}

  submitActivation(is_valid) {
  	this.submitted = true;
  	if(is_valid) {
        this.authService.activateAuth({token: this.code}).subscribe(resp => {
	      if(resp.success) {
	        this.auth.token = resp.auth_token;	
	        this.authService.updateAuth(this.auth);
	        this.router.navigateByUrl('/');
	      }
	      else {
	        this.helperService.presentToast(resp.message, 'danger');
	      }
	    },
	    err => {
	    	console.log(err);
	    	this.helperService.presentToast('Something went wrong. Please try again after sometime!', 'danger');
	    })
  	}
  }

}
