import { ToastController } from '@ionic/angular';
import { BloodPresureService } from '../../services/blood_presure.service';
import { BloodPresure } from '../models/blood_presure';
import {Component, OnInit, ViewChild} from '@angular/core';
import {Chart} from 'chart.js';

@Component({
    selector: 'app-blood-pressure',
    templateUrl: './blood-pressure.component.html',
    styleUrls: ['./blood-pressure.component.scss'],
})
export class BloodPressureComponent implements OnInit {
  submitted = false;
  blood_presure = new BloodPresure();
  blood_presures: Array<BloodPresure>;
  last_bp: BloodPresure;
  lineChart: any;
  @ViewChild('lineCanvas') lineCanvas;

  constructor(private bpService: BloodPresureService, public toastCtl: ToastController) { }

  ngOnInit() {

  }

  public loadBloodPresures() {
    this.bpService.getBloodPresures().subscribe(resp => {
       this.blood_presures = resp.blood_pressures;
       this.last_bp = this.blood_presures[0];
    }, 
    err => {
      this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
      console.log(err);
    })
  }

  public submitBloodPresure(is_valid) {
  	this.submitted = true;
  	if(is_valid) {
      this.bpService.recordBloodPresure(this.blood_presure).subscribe(resp => {
        this.blood_presure = new BloodPresure();
        this.submitted = false;
        this.presentToast('Blood pressure information recorded.');
      },
      err => {
      	this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
      	console.log(err);
      })
  	}
  }

  public removeBloodPresure(id, index) {
    if(confirm('Are you sure?')) {
      this.bpService.removeBloodPresure(id).subscribe(resp => {
        if(resp.success) {
          this.blood_presures = this.blood_presures.slice(index);
        }
        else {
          this.presentToast(resp.message, 'danger');
        }
      },
      err => {
        this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
        console.log(err);
      })
    }
  }

  segmentChanged(ev: any) {
    console.log(ev);
  }

  public loadBloodPressureChart() {
    this.bpService.getGraph().subscribe(resp => {
       this.loadChart(resp.labels, resp.low, resp.high, resp.pulse);
    }, 
    err => {
      this.presentToast('Something went wrong. Please try after sometimes!', 'danger');
    })
  }
  
  public loadChart(labels, low, high, pulse) {
      this.lineChart = new Chart(this.lineCanvas.nativeElement, {
          type: 'line',
          data: {
              labels: labels,
              datasets: [
                  {
                      fill: false,
                      label: 'Systolic',
                      backgroundColor: 'rgba(75,192,192,0.4)',
                      borderColor: 'rgba(75,192,192,1)',
                      borderCapStyle: 'butt',
                      borderJoinStyle: 'miter',
                      pointBorderColor: 'rgba(75,192,192,1)',
                      pointBackgroundColor: '#fff',
                      pointBorderWidth: 1,
                      pointHoverRadius: 5,
                      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                      pointHoverBorderColor: 'rgba(220,220,220,1)',
                      data: low,
                  },
                  {
                      fill: false,
                      label: 'Diastolic',
                      backgroundColor: 'rgba(35, 144, 196, 0.4)',
                      borderColor: 'rgba(35, 144, 196, 1)',
                      borderCapStyle: 'butt',
                      borderJoinStyle: 'miter',
                      pointBorderColor: 'rgba(75,192,192,1)',
                      pointBackgroundColor: '#fff',
                      pointBorderWidth: 1,
                      pointHoverRadius: 5,
                      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                      pointHoverBorderColor: 'rgba(220,220,220,1)',
                      data: high,
                  },
                  {
                      fill: false,
                      label: 'Pulse',
                      backgroundColor: 'rgba(234, 67, 53, 0.4)',
                      borderColor: 'rgba(234, 67, 53, 1)',
                      borderCapStyle: 'butt',
                      borderJoinStyle: 'miter',
                      pointBorderColor: 'rgba(234, 67, 53,1)',
                      pointBackgroundColor: '#fff',
                      pointBorderWidth: 1,
                      pointHoverRadius: 5,
                      pointHoverBackgroundColor: 'rgba(234, 67, 53,1)',
                      pointHoverBorderColor: 'rgba(234, 67, 53,1)',
                      data: pulse,
                  }
              ]
          }
      });
  }

  async presentToast(message, color = 'light') {
    const toast = await this.toastCtl.create({
      message: message,
      duration: 5000,
      showCloseButton: true,
      position: 'top',
      closeButtonText: 'Close',
      color: color
    });
    toast.present();
  }
}
